header {
  height: 10vh;
  line-height: 10vh;
}
header .container {
  padding-top: 15px;
}
header ul li {
  margin-left: 30px;
}
header ul li a {
  color: rgb(243, 232, 232);
  transition: 0.5s;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 1px;
}
header ul li a:hover {
  color: #01ffb3;
}
.home-btn {
  padding: 18px 25px;
  border-radius: 6px;
  transition: 0.3s all ease;
  cursor: pointer;
  height: fit-content;
  color: #01ffb3;
}
.home-btn {
  background: linear-gradient(145deg, #121213, #0f0e0e);
  box-shadow: 4px 4px 8px #0e0d0d, -4px -4px 8px #0e0c0c;
  margin-right: 2%;

}
.home-btn:hover {
  background: #01ffb3;
  color: rgb(250, 248, 248);
}
.open {
  display: none;
}
.header.active {
  height: 12vh;
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
  background-color: #080808;
  box-shadow: -1px 11px 24px -11px rgba(0, 0, 0, 0.75);
  transition: 0.5s;
}
@media (max-width: 768px) {
  header ul {
    color: rgb(247, 242, 242);
    padding-top: 30px;
  }
  .nav-links-mobile {
    position: absolute;
    display: block;
    list-style: none;
    box-shadow: rgba(242, 242, 245, 0.23) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    left: 0;
    top: 0;
    transition: all 0.5s ease-in-out;
    width: 60%;
    height: 100vh;
    background-color: rgb(7, 7, 7);
    z-index: 888;
  }
  header ul li {
    
    width: 100%;
    transition: all 0.5s ease-in-out;
    text-transform: uppercase;
    line-height: 70px;
  }
  header .link {
    display: none;
  }
  .close,
  .open {
    position: absolute;
    top: 35px;
    right: 20px;
    color: #01ffb3;
    display: block;
    font-size: 25px;
    z-index: 999;
  }
  .close {
    right: 45%;
  }
  .close.home-btn {
    background: linear-gradient(145deg, #080808, #080808);
    color: #01ffb3;
    border-radius: 50%;
    padding: 0;
    width: 60px;
    height: 60px;
    line-height: 60px;
  }
  .header.active {
    height: 10vh;
  }
}
  .logo{
    color: #faf6f6;
  }